import React, { useContext, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEllipsisH } from "@fortawesome/free-solid-svg-icons";

import {
  Card,
  CardBody,
  CustomInput,
  UncontrolledDropdown,
  DropdownItem,
  DropdownToggle,
  DropdownMenu,
} from "reactstrap";
import AppContext from "./context";

const TodosCard = ({ todo }) => {
  const [isCheck, setIsCheck] = useState(false);
  const { handleDelete, handleEdit, editTodoText } = useContext(AppContext);
  const { text, id } = todo;
  console.log(editTodoText);

  return (
    <Card className="mb-4">
      <CardBody className="d-flex justify-content-between">
        <div className="row no-gutters">
          <div className="col-auto align-middle">
            <CustomInput
              type="checkbox"
              id={id}
              onChange={({ target }) => {
                setIsCheck(target.checked);
              }}
            />
          </div>
          <div className="col align-top">
            <h5 className="mb-0">{isCheck ? <del>{text}</del> : text}</h5>
          </div>
        </div>
        <UncontrolledDropdown className="btn-reveal-trigger">
          <DropdownToggle
            color="link"
            size="sm"
            className="btn-reveal text-600 btn btn-link btn-sm"
            boundary="viewport">
            <FontAwesomeIcon icon={faEllipsisH} className="fs--1" />
          </DropdownToggle>
          <DropdownMenu className="border py-0" right>
            <div className="bg-white py-2">
              <DropdownItem onClick={() => handleEdit(id)}>Edit</DropdownItem>
              <DropdownItem>Move</DropdownItem>
              <DropdownItem>Resize</DropdownItem>
              <DropdownItem divider />
              <DropdownItem className="text-warning">Archive</DropdownItem>
              <DropdownItem
                className="text-danger"
                onClick={() => {
                  handleDelete(id);
                }}>
                Delete
              </DropdownItem>
            </div>
          </DropdownMenu>
        </UncontrolledDropdown>
      </CardBody>
    </Card>
  );
};

export default TodosCard;
